@import '../../themes/clubwho-theme.scss';

.generic-box {
	background: $primary;
	width: 0px;
	height: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	text-align: center;
	position: absolute;
	bottom: -100px;
}

.generic-box-text {
	font-weight: 700;
	font-size: 34px;
	margin: 0 5px;
	white-space: nowrap;
	transform-origin: center;
	margin-left: -100%;
	margin-right: -100%;
}